header {
	padding-top: 10px;
	padding-bottom: 10px;
	display: flex;
	justify-content: space-between;
	max-width: 1440px;
	margin: auto;
	border-bottom: 1px solid rgba(0, 0, 0, 0.1);
	.logo-holder {
		display: flex;
		padding: 10px;
		align-items: center;
		font-weight: 600;
		color: $base-800;
		.logo {
			display: flex;
			align-items: center;
			justify-content: center;
			font-size: 32px;
			background-color: $base-800;
			color: $base-100;
			height: 64px;
			width: 64px;
			margin-right: 20px;
			border-radius: 50%;
		}
		.logo-text {
			flex: 1;
		}
	}
	nav {
		display: flex;
		align-items: center;
		ul {
			display: flex;
			list-style-type: none;
			gap: 5px;
			li {
				display: inline-block;
				a {
					display: inline-block;
					padding: 10px 20px;
					color: $base;
					&:hover {
						background-color: $base-100;
						border-radius: 10px;
						text-decoration: none;
					}
				}
			}
		}
		.mobile-toggle {
			color: $base-800;
			display: none;
			padding: 10px;
			@media(max-width:768px) {
				display: inline-block;
				position: absolute;
				top: 20px;
				right: 20px;
			}
		}
	}
	@media (max-width:1024px) {
		flex-direction: column;
		align-items: center;
	} 
	@media (max-width:768px) {
		flex-direction: column;
		align-items: center;
		nav {
			margin-top: 10px;
			width: 100%;
			ul {
				display: none;
				flex-direction: column;
				text-align: center;
				width: 100%;
				a {
					width: 100%;
				}
				&.active {
					display: flex;
				}
			}

		}
	}
}