.logos {
	background-color: $base-100;
	border-radius: 30px;
	padding: 30px 0;
	@media (max-width:1440px) {
		border-radius: 0px;
	}
	.marquee {
		width: 100vw;
		max-width: 100%;
		height: 128px;
		overflow: hidden;
		position: relative;
		.track {
			position: absolute;
			white-space: nowrap;
			will-change: transform;
			animation: marquee 40s linear infinite;
			display: flex;
			gap: 10px;
		}
	}
}
@keyframes marquee {
	0% {
		transform: translateX(0);
	}
	100% {
		transform: translateX(-50%);
	}
}